body {
  background-color: whitesmoke;
}

#root {
  display: flex;
}

.div-items {
  width: 80%;
}

.items-block {
  background-color: white;
  width: 15%;
  height: 400px;
  border: 1px solid black;
  border-radius: 20px;
  margin: 40px;
  display: inline-grid;
}

.item-details {
  padding: 10px 20px;
}

.div-btn-card {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
}

.btn-add-to-cart {
  width: 90%;
  border: none;
  border-radius: 20px;
  background-color: lightgreen;
  cursor: pointer;
  margin: 0;
  height: 40px;
}

.div-cart {
  position: fixed;
  left: 80%;
  width: 18%;
  height: 90vh;
  padding: 10px;
  border-radius: 20px;
  margin-top: 40px;
  background-color: white;
}

.cart-summary {
  h2 {
    text-align: center;
  }
}

.card-item-details {
  height: 75vh;
  overflow: auto;
  display: inline-block;
  width: 100%;
}

.cart-item-box{
  width: 100%;
  float: left;
}

.cart-item-name-box {
  border-radius: 10px;
  float: left;
  min-height: 25px;
  max-width: 60%;
  margin: 2px 0;
  padding: 4px 8px;
  background-color: whitesmoke;
}

.cart-item-price-box {
  border-radius: 10px;
  float: right;
  min-height: 25px;
  margin: 2px 0;
  padding: 4px 8px;
  background-color: whitesmoke
}

.cart-total-box {
  font-family:'Courier New', Courier, monospace;
  background-color: greenyellow;
  padding: 8px 16px;
  width: 88%;
  border-radius: 20px;
  text-align: center;
  font-size: 24px;
  position: relative;
  top: 20px;

  h4 {
    margin: 0;
  }
}